html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "OpenSans", sans-serif;
  min-width: 320px;
}

a {
  color: inherit;
  text-decoration: none;
  font-family: "OpenSans";
}

:focus-visible {
  outline: none;
}

p {
  margin: 0;
  font-family: "OpenSans";
}

img {
  max-width: 100%;
}

li {
  list-style-type: none;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3 {
  margin: 0;
  font-family: "OpenSans";
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

.btn-reset {
  padding: 0;
  font-family: "OpenSans";
  border: none;
  background-color: transparent;
  cursor: pointer;
}